<template lang="pug">
.add-to-cart
    template(v-if="is_added_to_cart")
        vs-button(
            @click="removeFromCart(product.id)"
            color="success"
            flat
            key="added"
        ) In cart

    template(v-else)
        vs-button(
            style="color: #000"
            color="#feda01"
            @click="goToSite"
            key="add"
        ) Show on Rarible
</template>

<script>
import { mapActions } from 'vuex'

export default {
    name: 'AddToCart',
    props: {
        product: {
            type: Object,
            required: true
        }
    },
    computed: {
        is_added_to_cart() {
            return this.$store.getters['cart/is_added_to_cart'](this.product.id)
        }
    },
    methods: {
        ...mapActions({
            addToCart: 'cart/addToCart',
            removeFromCart: 'cart/removeFromCart'
        }),
      goToSite () {
        window.location.href = this.product.href
      }
    }
}
</script>
